import React, { useContext, useEffect, useState } from "react";
import { Drawer, Button, Empty } from "antd";
import { PhoneOutlined } from "@ant-design/icons";
Card;
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/Shared/shadncn-ui/table"; // shadcn table
import { Card } from "@/Shared/shadncn-ui/card";
import { CallButton } from "./callButton";
import RecentlNotification from "./recentNotifications";
import { IoIosArrowForward } from "react-icons/io";
import RoomMapBox from "./Room";
import { WebSocketContext, WebSocketProvider } from "@/Context/WebSoketHook";
import { subscribeToMqtt } from "@/api/deviceReports";
import { decodeHeartBreath } from "@/utils/helper";
import { useNavigate } from "react-router-dom";
import AlermLogs from "./AlarmLogs";
import ActiveCriticalAlerts from "./ActiveCriticalAlerts";
import { MdKeyboardDoubleArrowRight } from "react-icons/md";
import NotificationConfiguration from "./NotificationConfiguration";

const RoomOverviewDrawer = ({ open, setOpen, data }) => {
  const onClose = () => setOpen(false);
  const navigate = useNavigate();
  return (
    <WebSocketProvider
      deviceId={
        data?.details?.length > 0
          ? data?.details
              ?.filter((item) => item.device_no)
              .map((item) => item.device_no)
              .join(",")
          : ""
      }
    >
      <Drawer
        title={null}
        onClose={onClose}
        closeIcon={<MdKeyboardDoubleArrowRight size={24} />}
        size="large"
        className="relative "
        open={open}
      >
        {/* Header */}
        <div className="flex justify-between items-center mb-6 border-b pt-2 pb-4 fixed w-[calc(736px-48px)] top-[calc(57px)] bg-white z-50">
          <div>
            <h2 className="text-2xl font-bold">
              {data?.name || "Name not found"}
            </h2>
            <p className="text-lg text-gray-600">
              {data?.room || "Room No. Not Found"}
            </p>
          </div>
          <Button
            size={"large"}
            icon={<IoIosArrowForward />}
            iconPosition={"end"}
            type="primary"
            onClick={() => {
              window.scrollTo(0, 0);
              navigate(
                `/supporter/elderlies/elderly-profile/${data.id}?tab=overview`
              );
            }}
          >
            View Profile
          </Button>
        </div>
        <div className="mt-[77px]"></div>
        {/* <LiveHeartAndBreathRate rooms={data?.details} /> */}
        <ActiveCriticalAlerts elderlyId={data?.id} />
        {/* Rooms View */}
        <div className="mb-8 mt-12">
          <div className="flex items-center justify-center gap-2">
            {/* <div className="size-3 bg-slate-500 rounded-sm"></div> */}
            <h3 className="text-base font-medium text-nowrap">Room Map</h3>
            <hr className="w-full m-0" />
          </div>
          {data?.details
            ?.filter((item) => item?.device_no)
            .map((item, indx) => {
              return <RoomMapBox key={indx} data={item} elderlyId={data?.id} />;
            })}

          {data?.details?.filter((item) => item?.device_no)?.length == 0 && (
            <Empty description="No Room" className="mt-4"></Empty>
          )}
        </div>
        {/* <div className="mb-8 mt-8">
          <NotificationConfiguration
            room_details={
              data?.details?.filter((item) => item?.room_type == 2)[0]
            }
            elderly_id={data?.id}
          />
        </div> */}
        <div className="mb-8 mt-8">
          <AlermLogs
            id={data?.details?.filter((item) => item?.room_type == 2)[0]._id}
          />
        </div>
      </Drawer>
    </WebSocketProvider>
  );
};

export default React.memo(RoomOverviewDrawer);
